
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Hammersmith+One&family=Itim&family=League+Spartan:wght@300;500&family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&family=Passions+Conflict&family=Permanent+Marker&family=Raleway&family=Source+Code+Pro&family=Unbounded:wght@200;300;400;500;600;800;900&display=swap');
h1{
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;
  opacity: .3;
  font-size: 40px;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: center;
}
h1::first-letter{
  font-size: 60px;
  color: crimson;
  opacity: .9;
}
.App{
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
}
.standardLabel{
  width: 100%;
}
.ListCaller{
  margin-top: 1.5rem;
}
.List{
  box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 50px;
  padding: 0.2rem;
  margin-top: 1rem;
  text-align: center;
  
}
h3{
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  margin-top: 40px;
}
.MuiInput-root {
  border-bottom: 2px solid #3E54AC !important;
  width: 24rem;
}

@media only screen and (max-width: 600px) {
  .MuiInput-root {
    border-bottom: 2px solid #3E54AC !important;
    width: 16rem;
  }
  h1{
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    opacity: .3;
    font-size: 25px;
    margin-top: 1rem;
    margin-bottom: 2rem;
    text-align: center;
  }
}